.experience {
    background-color: #f4f4f4;
    padding: 100px 0;
  }
  
  .experience .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .experience h2 {
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 30px;
  }
  
  .experience .job {
    background-color: #fff;
    border-left: 4px solid #007bff;
    padding: 20px;
    margin-bottom: 20px;
    transition: transform 0.3s;
  }
  
  .experience .job:hover {
    transform: translateX(10px);
  }
  
  .experience .job h3 {
    margin-bottom: 10px;
    font-size: 1.8rem;
  }
  
  .experience .job .date {
    color: #777;
    font-style: italic;
    margin-bottom: 10px;
  }
  
  .experience .job p {
    font-size: 1.1rem;
    line-height: 1.8;
  }
  