.contact {
    background-color: #f4f4f4;
    padding: 100px 0;
    text-align: center;
  }
  
  .contact .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .contact h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }
  
  .contact-info {
    font-size: 1.2rem;
  }
  
  .contact-info a {
    display: block;
    color: #007bff;
    text-decoration: none;
    margin: 10px 0;
  }
  
  .contact-info a:hover {
    text-decoration: underline;
  }
  