.projects {
    background-color: #f4f4f4;
    padding: 100px 0;
    text-align: center;
  }
  
  .projects .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .projects h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }
  
  .project-cards {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .project-card {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.2);
    padding: 20px;
    margin: 10px;
    flex: 1;
    max-width: 300px;
    text-align: center;
  }
  
  .project-icon {
    margin-bottom: 15px;
  }
  